.film_actors {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}


.film_title {
    width: 100%;
    text-align: center;
    margin: 16px;
    font-size: 64px;
    margin-bottom: 30px;
}

.film_name {
    text-align: center;
    margin-top: 16px;
    line-height: 20px;
    font-size: 18px;
    font-weight: 600;
}

.actor {
    width: 200px;
    height: 200px;
    position: relative;
}

.film_img {
    width: 100%;
    height: 100%;
    position: relative;
    
    overflow: hidden;
    border-radius: 50%;
}

.portrait {
    width: 100%;
    height: auto;
}

.landscape {
    width: auto;
    height: 100%;
}

.home_icon {
    font-size: 40px;
    position: absolute;
    top: 20px;
    right: 20px;
}

.home_icon a {
    color: #000;
}

.kino {
    width: 50vw;
    height: 29.4vw;
    border-radius: 20px;
    overflow: hidden;
    margin-left: 25vw;
    margin-top: 100px;
}